import { useEffect, useRef, useState } from "react";

export function InfiniteScroll({ onPageChange, currentPage, totalPage }) {
    const infiniteComponentRef = useRef(null)
    const [page, setPage] = useState(currentPage > 1 ? currentPage : 1);

    useEffect(() => {
        const options = {
            root: null,
            rootMargin: "500px 20px",
            threshold: 1.0
        };
        const observer = new IntersectionObserver(entries => {
            const target = entries[0]
            if (target.isIntersecting) {
                setPage(page => page + 1)
            }
        }, options);

        if (infiniteComponentRef.current) {
            observer.observe(infiniteComponentRef.current);
        }
    }, [])

    useEffect(() => {
        if (page !== currentPage && totalPage >= page) {
            onPageChange(page)
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [page])

    return <div ref={infiniteComponentRef} />
}