import { CardGroup, Col, Row, Spinner } from "reactstrap";
import { Article, InfiniteScroll } from "../../components";
import { useDispatch, useSelector } from 'react-redux'
import { getListMovies, updateActivityVideo } from "../../features/listview";
import { useEffect } from "react";


export default function HomeScreen() {
    const dispatch = useDispatch()
    const { loading, movies, pagination, isMouted, currentUser } = useSelector(state => state.listview)

    const handleOnPageChange = (value) => {
        dispatch(getListMovies(value))
    }
    const handleUserVote = (item, type) => {
        dispatch(updateActivityVideo(item, type))
    }
    useEffect(() => {
        if (isMouted) {
            dispatch(getListMovies())
        }
    }, [dispatch, isMouted])

    return <Row className="justify-content-center">
        <Col md={8}>
            <>
                {movies.length !== 0 && <CardGroup className="flex-column">
                    {movies.map(it => <Article item={it} key={`movie-${it.id}`} isShowButtonVote={!!currentUser} handleUserVote={handleUserVote} />)}
                    <InfiniteScroll onPageChange={handleOnPageChange} currentPage={pagination.currentPage} totalPage={pagination.totalPage} />
                </CardGroup>}
            </>
            {loading && <div className="d-flex justify-content-center"><Spinner /></div>}
        </Col>
    </Row>
}