import React, { useEffect } from 'react';
import {
  HashRouter as Router,
  Switch,
  Route,
} from "react-router-dom";

import { HomeScreen, ShareScreen, SignUpScreen } from "./screens";
import { Layout, PrivateRoute } from "./components";
import { useDispatch } from 'react-redux';
import { getCurrentUser } from "./features/listview";
import { ToastContainer } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

function App() {
  const dispatch = useDispatch()

  useEffect(() => {
    dispatch(getCurrentUser())
  }, [dispatch])

  return (
    <Router>
      <ToastContainer />
      <Switch>
        <Route path="/sign_up">
          <SignUpScreen />
        </Route>
        <Layout>
          <Route path="/" exact>
            <HomeScreen />
          </Route>
          <PrivateRoute path="/share-a-movie">
            <ShareScreen />
          </PrivateRoute>
        </Layout>

      </Switch>
    </Router>
  );
}

export default App;
