import { createSlice } from '@reduxjs/toolkit';
import baseAPI from "../lib/axios";
import { toast } from "react-toastify";


function merge(arr1, arr2) {
  return arr1.filter(elem => !arr2.find(subElem => subElem["id"] === elem["id"]))
    .concat(arr2)
    .sort((a, b) => b.id - a.id);
}

const listViewSlice = createSlice({
  name: 'listView',
  initialState: {
    loading: false,
    movies: [],
    currentUser: null,
    pagination: {
      currentPage: 1,
      totalPage: null
    },
    isMouted: false,
  },
  reducers: {
    setLoading: function (state, action) {
      state.loading = action.payload
    },
    setMovies: function (state, action) {
      const { data, pagination } = action.payload
      state.movies = merge(state.movies, data)
      state.pagination = { currentPage: pagination.page, totalPage: pagination.pages }
    },

    setCurrentUser: function (state, action) {
      state.currentUser = action.payload
      state.isMouted = true
    },
    setMounted: function (state) {
      state.isMouted = true
    },
    updateListMovies: function (state, action) {
      const { index, data } = action.payload
      state.movies[index] = data
    },
    removeCurrentUser: function (state) {
      state.currentUser = null
    }
  },
});

export const getListMovies = (page = 1) => async (dispatch) => {
  dispatch(setLoading(true))
  const moviess = await baseAPI.get('movie', { params: { page } })
  dispatch(setMovies(moviess))
  dispatch(setLoading(false))
}


export const getCurrentUser = () => async (dispatch) => {
  try {
    const user = await baseAPI.get('user/me')
    const { access_token, object } = user
    localStorage.setItem('access_token', access_token)
    dispatch(setCurrentUser(object))
  } catch (error) {
    dispatch(setMounted())
  }
}

export const login = (data) => async (dispatch) => {
  try {
    const user = await baseAPI.post('user/sign_in', data)
    const { access_token, object } = user
    localStorage.setItem('access_token', access_token)
    dispatch(setCurrentUser(object))
  } catch (error) {
    toast.error(error.error)

  }
}

export const logout = () => async (dispatch) => {
  const result = await baseAPI.delete('user/sign_out')
  toast.success(result.message)
  dispatch(removeCurrentUser())

}

export const updateActivityVideo = (movie_id, status) => async (dispatch, getState) => {
  const { listview: { movies } } = getState()
  const movie = await baseAPI.post(`movie/like/${movie_id}`, null, { params: { status } })

  if (movie.object === null) {
    const index = movies.findIndex(it => it.id === movie_id)
    const item = movies[index]
    const currentAction = item['action']
    const data = {
      ...item,
      action: "un_voted",
      ...(currentAction === 'voted_up' && {
        voted_up: item.voted_up - 1
      }),
      ...(currentAction === 'voted_down' && {
        voted_down: item.voted_down - 1
      }),
    }
    dispatch(updateListMovies({ index, data }))
  }
  else {
    const index = movies.findIndex(it => it.id === movie.object.id)
    dispatch(updateListMovies({ index, data: movie.object }))
  }
}

export const { setLoading, setMovies, setCurrentUser, setMounted, updateListMovies, removeCurrentUser } = listViewSlice.actions;

export default listViewSlice.reducer;
