import { useState } from 'react';
import { Container, Form, Row, Col, Button, Collapse, Nav, Navbar, NavItem, NavbarToggler } from 'reactstrap';
import { useSelector, useDispatch } from 'react-redux'
import { Link } from 'react-router-dom';
import { useForm } from "react-hook-form";

import { logout, login } from "../../features/listview";


export function Layout({ children }) {
  const dispatch = useDispatch()
  const [isOpen, setIsOpen] = useState(false)
  const { currentUser } = useSelector(state => state.listview)
  const { register, handleSubmit, formState: { isSubmitting } } = useForm();

  const onsubmit = (value) => {
    dispatch(login(value))
  }

  return (
    <Container>
      <Navbar color="light" light expand="md">
        <Link to="/">
          Remitano Test
          </Link>
        <NavbarToggler onClick={() => setIsOpen(!isOpen)} />
        <Collapse isOpen={isOpen} navbar>
          <Nav className="ml-auto w-50" navbar>
            <NavItem className="w-100">
              {currentUser
                ? <div className="text-right">
                  <span className="mr-2">wellcome <span className="text-primary">{currentUser.username}</span>!</span>
                  <Link to="/share-a-movie">
                    <Button className="mr-2" color="primary">Share a movie </Button>
                  </Link>
                  <Button onClick={() => dispatch(logout())}>Logout</Button>
                </div>
                :
                <Form onSubmit={handleSubmit(onsubmit)}>
                  <Row form >
                    <Col md={4}>
                      <input type="username" name="username" placeholder="Username" className="form-control" {...register('username')} />
                    </Col>
                    <Col md={4}>
                      <input type="password" name="password" placeholder="Password" className="form-control" {...register('password')} />
                    </Col>
                    <Col md={4}>
                      <Button block color="success" type="submit" disabled={isSubmitting}>login / register</Button>
                    </Col>
                  </Row>
                  <Row>
                    <Col>
                      <Link to='/sign_up'>I dont have account</Link>
                    </Col>
                  </Row>
                </Form>}
            </NavItem>
          </Nav>
        </Collapse>
      </Navbar>
      {children}
    </Container>
  )
}