import { Button, Col, Form, FormGroup, Label, Row } from "reactstrap";
import { useForm } from "react-hook-form";
import { yupResolver } from '@hookform/resolvers/yup';
import * as yup from "yup";
import { useHistory } from "react-router-dom";
import { toast } from 'react-toastify';

import baseApi from "../../lib/axios";

const schema = yup.object().shape({
    link: yup.string().url("value is not url").required("Youtube url is required"),
});


export default function ShareScreen() {
    const { register, handleSubmit, formState: { errors, isSubmitting } } = useForm({ resolver: yupResolver(schema) });
    const history = useHistory()

    const onSubmit = async (data) => {
        try {
            const movie = await baseApi.post('movie/share', data)
            if (movie.object) {
                history.push('/')
            }
        } catch (error) {
            toast(error.error, { type: "error" })
        }
    };

    return <Row className="justify-content-center mt-4">
        <Col md={8} className="py-4 px-3 border">
            <Form onSubmit={handleSubmit(onSubmit)} autoComplete="off">
                <FormGroup row >
                    <Label for="link" md={3}>Youtube URL:</Label>
                    <Col md={7}>
                        <input type="text" className="form-control" {...register('link')} />
                        <p className="text-danger">{errors.link?.message}</p>
                    </Col>
                </FormGroup>
                <Row>
                    <Col md={3}></Col>
                    <Col md={7}>
                        <Button block color="success" type="submit" disabled={isSubmitting}>Share</Button>
                    </Col>
                </Row>
            </Form>
        </Col>
    </Row>
}