import { memo, useMemo } from "react";
import { Card, CardBody, CardSubtitle, CardText, CardTitle, Col, Row } from "reactstrap";
import { ReactComponent as VoteLogo } from './vote.svg';
import { ReactComponent as VotedLogo } from './voted.svg';
import style from "./index.module.css";
import classnames from "classnames";

export const Article = memo(({ item, handleUserVote, isShowButtonVote }) => {
    const onClick = (type) => {
        handleUserVote(item.id, type)
    }

    const renderLikeIcon = useMemo(() => {
        const type = item.action
        if (type === 'un_voted') {
            return <Col md={4} className="text-right">
                <div className={style.button_vote} onClick={() => onClick("voted_up")}>
                    <LikeIcon />
                </div>
                <div className={style.button_vote} onClick={() => onClick("voted_down")}>
                    <LikeIcon type="voted_down" unvote />
                </div>
            </Col>
        }
        return <Col md={4} className="text-right">
            <div className={style.button_vote} onClick={() => onClick("un_voted")}>
                <LikeIcon type={type} voted />
            </div>
        </Col>

            // eslint-disable-next-line
    }, [item.action])

    return <Card className="flex-row my-4">
        <iframe
            src={`https://www.youtube.com/embed/${item.youtube_id}`}
            title="YouTube video player"
            frameBorder="0"
            allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
            allowFullScreen>
        </iframe>
        <CardBody>
            <CardTitle className='text-danger' tag="h5">{item.title}</CardTitle>
            <CardSubtitle tag="div" className="">
                <Row>
                    <Col md={8}>
                        Share by <span className="font-weight-bold"> {item.user.username}</span>
                    </Col>
                    {isShowButtonVote && renderLikeIcon}
                </Row>
            </CardSubtitle>
            <div>
                <span>
                    {item.voted_up} <LikeIcon />
                </span>
                <span className="ml-2">
                    {item.voted_down} <LikeIcon type="voted_down" /></span>
            </div>
            <CardText className="limit-text">{item.description}</CardText>
        </CardBody>
    </Card>
})

function LikeIcon({ type = 'voted_up', voted }) {
    return voted
        ? <VotedLogo className={classnames(style.ic_vote, { [style.voted_down]: type === 'voted_down' })} />
        : <VoteLogo className={classnames(style.ic_vote, { [style.voted_down]: type === 'voted_down' })} />
}