import { Button, Col, Container, Form, FormGroup, Row } from "reactstrap";
import * as yup from "yup";
import { yupResolver } from '@hookform/resolvers/yup';
import { useForm } from "react-hook-form";
import { toast } from 'react-toastify';
import { useHistory } from "react-router-dom";
import { setCurrentUser } from "../../features/listview";
import { useDispatch } from 'react-redux'

import baseApi from "../../lib/axios";
import './index.css'


const SignUpSchema = yup.object().shape({
    username: yup.string().required('Username is required'),
    password: yup
        .string()
        .required('Password is required')
        .min(6, 'Password must be at least 6 characters'),
    password_confirmation: yup
        .string()
        .required('Password confirm is required')
        .oneOf([yup.ref('password'), null], 'Passwords must match'),
});

export default function SignUpScreen() {
    const { register, handleSubmit, formState: { errors, isSubmitting } } = useForm({ resolver: yupResolver(SignUpSchema) });
    const history = useHistory()
    const dispatch = useDispatch()

    const submit = async (data) => {
        try {
            const user = await baseApi.post('user/register', data)
            const { access_token, object } = user
            localStorage.setItem('access_token', access_token)
            dispatch(setCurrentUser(object))
            history.push('/')
        } catch (error) {
            toast(error.error, { type: "error" })
        }
    }
    return <div className="login-page">
        <Container className="h-100">
            <Row className="justify-content-center">
                <Col md={5}>
                    <Form onSubmit={handleSubmit(submit)} className="login-form">
                        <div className="login-title">Sign Up</div>
                        <FormGroup>
                            <input className="form-control" type="text" placeholder="Username" {...register('username')} />
                            <p className="text-danger">{errors.username?.message}</p>
                        </FormGroup>
                        <FormGroup>
                            <input className="form-control" type="password" placeholder="Password"  {...register('password')} />
                            <p className="text-danger">{errors.password?.message}</p>
                        </FormGroup>
                        <FormGroup>
                            <input className="form-control" type="password" placeholder="Password confirmation"  {...register('password_confirmation')} />
                            <p className="text-danger">{errors.password_confirmation?.message}</p>
                        </FormGroup>
                        <Button block color="success" disabled={isSubmitting}>Sign up</Button>
                    </Form>
                </Col>
            </Row>
        </Container>
    </div >
}